const structureBtn = document.querySelectorAll('.structure__btn');

structureBtn.forEach( btn => {
    btn.addEventListener('click', ()=> {
        btn.classList.toggle('close');
        let structureBody = btn.parentElement.parentElement.querySelector('.structure__body');
        structureBody.classList.toggle('show');
    })
});
